<template>
  <div class="hello">
    <v-slider :slides='slides' :inv='slideSpeed'></v-slider>
    <detail></detail>
    <more></more>
    <foot></foot>
  </div>
</template>

<script>
import VSlider from './item/slider'
import Foot from './item/foot'
import More from './item/more'
import Detail from './item/detail'
export default {
  data () {
    return {
      slideSpeed: 3000,
      slides: [
        {
          src: require('../../assets/pic-1.png')
        },
        {
          src: require('../../assets/pic-1.png')
        },
        {
          src: require('../../assets/pic-1.png')
        },
        {
          src: require('../../assets/pic-1.png')
        },
        {
          src: require('../../assets/pic-1.png')
        }
      ]
    }
  },
  components: {
    VSlider,
    Foot,
    More,
    Detail
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



